define('fusor-ember-cli/components/accordion-item', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Component.extend({
    isOpen: false,
    actions: {
      openItem: function () {
        this.set("isOpen", this.toggleProperty("isOpen"));
      }
    }
  });

});